import { Button, createMuiTheme, Paper, TextField, ThemeProvider, useMediaQuery } from "@material-ui/core";
import { blue, green } from "@material-ui/core/colors";
import Axios from "axios";
import config from '../../config.json';
import React from "react";


export default function ChangePassword({user}) {
    var [state,setState]= React.useState(false);
    var [page,setPage] = React.useState(0);
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
    const theme2 = React.useMemo(
      () =>
        createMuiTheme({
          palette: {
            type: prefersDarkMode ? 'dark' : 'light',
          },
        }),
      [prefersDarkMode],
    );

    const theme = createMuiTheme({
      palette: {
        primary: {
          light: '#757ce8',
          main: '#AA00FF',
          dark: '#002884',
          contrastText: '#fff',
        },
        secondary: {
          light: '#ff7961',
          main: blue[600],
          dark: green[700],
          contrastText: '#000',
        },
        type: prefersDarkMode ? 'dark' : 'light',
      },
    });
    var [oldpass,chngoldpass] = React.useState("");
    var [newpass,chngnewpass] = React.useState("");
    var submitFn = (e) => {
        e.preventDefault();
        if(newpass!=oldpass) {
            alert("Passwords dont match");
            return;
        }
        var frm = new FormData();
        frm.append("userid",user.userid);
        frm.append("password",newpass)
        Axios.post(config.host+"api/change_password.php",frm).then((resp) => {
            if(!resp.data.error) {
                alert("Change password Success");
                chngnewpass("");
                chngoldpass("");
            } else {
                alert(resp.data.status)
            }
        })
    }
    return(
        <div style={{height:"100%"}}>
            <div style={{ textAlign: "center", fontSize: "30px",color:"#fff" }}>Change Password</div>
                <ThemeProvider theme={theme2}>
                    <form onSubmit={submitFn} style={{display:"flex",height:"100%",flexDirection:"column",justifyContent:"center",alignContent:"center",alignItems:"center"}} >
                        <div style={{width:"100%",maxWidth:"320px",textAlign:"center"}}>
                            <TextField margin="normal" required value={oldpass} onChange={(e)=>{chngoldpass(e.target.value)}} label="New Password" fullWidth type="text" />
                            <TextField margin="normal" required value={newpass} error={newpass==oldpass} onChange={(e)=>{chngnewpass(e.target.value)}} label="Repeat New Password" fullWidth type="text" />   
                            <Button type="submit" variant="outlined">Change Password</Button>

                        </div>
                    </form>
                </ThemeProvider>
                
        </div>
    )
}