import React from 'react';

import { Button } from '@material-ui/core';

import './page5.css';

class Page5 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};
    }

    download() {
        var blob = 'data:@file/png;base64,';

        // var csvURL = window.URL.createObjectURL(blob);
        var tempLink = document.createElement('a');
        tempLink.href = blob;
        tempLink.setAttribute('download', 'page5key.3db');
        tempLink.click();
    }

    render() {
        return (
            <div className="round2">
                <div className="page5">
                    <div className="desc">You will get the key if you know the exact file type</div>
                    <Button onClick={this.download} variant="contained" color="primary">Download</Button>
                </div>
            </div>
        );
    }
}

export default Page5;
