import React from 'react';

import { Button } from '@material-ui/core';

import RotateLeftIcon from '@material-ui/icons/RotateLeft';

import './page8.css';

class Page8 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            res: 7,
            desired: 9386,
            operations_done: { "b1": 0, "b2": 0, "b3": 0, "b4": 0, "b5": 0, "b6": 0 },
            clickCount: 0,
            key: "Key: jlshdaihdwaw54534234654432",
            unlocked: false
        };

        this.xor = this.xor.bind(this);
        this.and = this.and.bind(this);
        this.mul = this.mul.bind(this);
        this.add = this.add.bind(this);
        this.reset = this.reset.bind(this);
        this.activeLine = this.activeLine.bind(this);

    }

    xor(val) {
        var unlocked = false, res = this.state.res ^ val;
        if (res === this.state.desired) unlocked = true;
        this.setState({ res, unlocked });
    }

    and(val) {
        var unlocked = false, res = this.state.res & val;
        if (res === this.state.desired) unlocked = true;
        this.setState({ res, unlocked });
    }

    mul(val) {
        var unlocked = false, res = this.state.res * val;
        if (res === this.state.desired) unlocked = true;
        this.setState({ res, unlocked });
    }

    add(val) {
        var unlocked = false, res = this.state.res + val;
        if (res === this.state.desired) unlocked = true;
        this.setState({ res, unlocked });
    }

    sub(val) {
        var unlocked = false, res = this.state.res - val;
        if (res === this.state.desired) unlocked = true;
        this.setState({ res, unlocked });
    }

    reset() {
        this.setState({ 
            res: 7, 
            unlocked: false,
            clickCount: 0,
            operations_done: { "b1": 0, "b2": 0, "b3": 0, "b4": 0, "b5": 0, "b6": 0 } 
        });
    }

    activeLine(line) {
        var operations_done = this.state.operations_done;
        var clickCount = this.state.clickCount;

        if (operations_done[line] == 0) clickCount++;
        
        operations_done[line] = 1;

        var unlocked = false;
        if (this.state.res == this.state.desired) unlocked = true;
        this.setState({ operations_done, clickCount });
    }

    render() {
        return (
            <div className="round1">

            <div className="page8">
                <div id="content">
                    <div className="instructions">You should use the below given operations on the result to get the desired output</div>
                    <div className="values">
                        <div className="result_box">
                            <div className="title">Calculated Result</div>
                            <div className="value">{this.state.res}</div>
                        </div>
                        <div className="result_box" style={{margin: "1px"}}>
                            <Button onClick={this.reset}>
                                <RotateLeftIcon fontSize="large" style={{color: "white"}} />
                            </Button>
                        </div>
                        <div className="result_box">
                            <div className="title">Desired Output</div>
                            <div className="value">{this.state.desired}</div>
                        </div>
                    </div>

                    <div className="op_title">Operations</div>
                    <div className="operations circle-container">
                        <div className="op circle_disabled"></div>
                        <div className="op b1" onClick={() => {this.xor(511); this.activeLine("b1"); }} >
                            <p style={{marginTop: "10px"}}>XOR</p>
                            <p>(res, 511)</p>
                            <div 
                                className="b1-line" 
                                style={{backgroundColor: (this.state.operations_done['b1'])?"rgb(9, 255, 0)":""}}></div>
                        </div>
                        <div className="op b3" onClick={() => {this.and(56); this.activeLine("b2"); }} >
                            <p style={{marginTop: "10px"}}>AND</p>
                            <p>(res, 56)</p>
                            <div 
                                className="b3-line"
                                style={{backgroundColor: (this.state.operations_done['b2'])?"rgb(9, 255, 0)":""}}></div>
                        </div>
                        <div className="op b4" onClick={() => {this.mul(3); this.activeLine("b3"); }} >
                            <p style={{marginTop: "10px"}}>MUL</p>
                            <p>(res, 3)</p>
                            <div 
                            className="b4-line"
                            style={{backgroundColor: (this.state.operations_done['b3'])?"rgb(9, 255, 0)":""}}></div>
                        </div>
                        <div className="op b6" onClick={() => {this.add(7); this.activeLine("b4"); }} >
                            <p style={{marginTop: "10px"}}>ADD</p>
                            <p>(res, 7)</p>
                            <div 
                                className="b6-line"
                                style={{backgroundColor: (this.state.operations_done['b4'])?"rgb(9, 255, 0)":""}}></div>
                        </div>
                       
                        <div className="op bc" onClick={() => {this.mul(19)}} 
                            style={{backgroundColor: (this.state.clickCount === 4)?"rgb(9, 255, 0)":"rgb(207, 83, 0)"}}
                        >
                        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 1000 1000">
                            <g>
                                {/* <path d="M500,10C229.4,10,10,229.4,10,500c0,270.6,219.4,490,490,490c270.6,0,490-219.4,490-490C990,229.4,770.6,10,500,10z M500,947.4C252.9,947.4,52.6,747.1,52.6,500C52.6,252.9,252.9,52.6,500,52.6c247.1,0,447.4,200.3,447.4,447.4C947.4,747.1,747.1,947.4,500,947.4z"/> */}
                                <path d="M452.1,392.4c0-24.5,6.4-83.1,69.2-83.1c59.7,0,69.2,52.2,69.2,83.1v18.1h52.2v-18.1c0-81-46.9-133.2-120.4-133.2c-73.5,0-120.4,52.2-120.4,133.2v18.1h52.2v-18.1H452.1z M641.7,427.6H400.9c-28.8,0-52.2,22.4-52.2,51.1V647c0,27.7,23.4,51.1,52.2,51.1h240.7c28.8,0,52.2-22.4,52.2-51.1V478.7C693.9,451,670.4,427.6,641.7,427.6L641.7,427.6z M538.3,575.6V614c0,9.6-7.5,17-17,17c-9.6,0-17-7.5-17-17v-38.3c-10.7-5.3-17-17-17-28.8c0-18.1,14.9-34.1,34.1-34.1s34.1,14.9,34.1,34.1C555.4,558.6,549,569.2,538.3,575.6L538.3,575.6z"/>
                            </g>
                        </svg>
                        </div>
                    </div>
                </div>
                <div style={{textAlign: "center", marginTop: "400px"}} id="key">{this.state.unlocked && this.state.key}</div>
            </div>
            </div>
        );
    }
}

export default Page8;