import React from "react";
import { AppBar, Divider, SwipeableDrawer as Drawer, Toolbar, Typography,ListItem,ListItemText,ListItemIcon,List, IconButton, Menu, MenuItem, Tabs, Tab, Avatar, Container, Paper, useMediaQuery, Button, TextField, CircularProgress } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { MoreVert, Translate,ArrowBack } from "@material-ui/icons";
import { makeStyles, ThemeProvider, withStyles } from '@material-ui/core/styles';
import { lightBlue,grey,green,blueGrey,red,pink,purple, } from '@material-ui/core/colors';
import * as Colors from '@material-ui/core/colors';
import { createMuiTheme } from '@material-ui/core/styles';

import './CustomScrollbar.css'
import Page1 from "./Round1/page1";
import Page2 from "./Round1/page2";
import Page3 from "./Round1/page3";
import Page4 from "./Round1/page4";
import Page5 from "./Round1/page5";
import Page6 from "./Round1/page6";
import Page7 from "./Round1/page7";
import Page8 from "./Round1/page8";
import Page9 from "./Round1/page9";
import Axios from "axios";
import config from '../config.json';


var drawerWidth = 50;

var pages = [
  {page:Page1,status:true},
  {page:Page2,status:false},
  {page:Page3,status:false},
  {page:Page4,status:false},
  {page:Page5,status:false},
  {page:Page6,status:false},
  {page:Page7,status:false},
  {page:Page8,status:false},
  {page:Page9,status:false},
]
var randomProperty = function (obj) {
  var keys = Object.keys(obj);
  return obj[keys[ keys.length * Math.random() << 0]];
};
var avatarColor = randomProperty(Colors)[500];
function Round1({user}) {

    var [mainPage,setMainPage] = React.useState(false);
    React.useEffect(() => {
      var frmData = new FormData();
      frmData.append("uid",user.userid)
      Axios.post(config.host+"api/getPageStatus.php",frmData).then((resp) => {
        if(!resp.data.error) {
          pages.map((page,index) => {
            pages[index].status = resp.data.data["q"+(index+2)]=="1";
            // console.log("q"+(index+2)+"="+"p"+index+"="+pages[index].status);
            setMainPage(true);
          })
        }
      })
      
    },[])
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
    const theme2 = React.useMemo(
      () =>
        createMuiTheme({
          palette: {
            type: prefersDarkMode ? 'dark' : 'light',
          },
        }),
      [prefersDarkMode],
    );

    const theme = createMuiTheme({
      palette: {
        primary: {
          light: '#757ce8',
          main: '#AA00FF',
          dark: '#002884',
          contrastText: '#fff',
        },
        secondary: {
          light: '#ff7961',
          main: Colors.blue[600],
          dark: green[700],
          contrastText: '#000',
        },
        type: prefersDarkMode ? 'dark' : 'light',
      },
    });

    var [state,setState]= React.useState(false);
    var [page,setPage] = React.useState(0);
    var [keyInput,setKeyInput] = React.useState("");
    var [navState,setNavState] = React.useState(true);
    var [loading,setLoading] = React.useState(false);
    var [errState,setErrState] = React.useState(false);
    var [errStatus,setErrStatus] = React.useState("");
    drawerWidth = navState?260:50;
    const useStyles = makeStyles((theme) => ({
      root: {
        display: 'flex',
      },
      appBar: {
        zIndex:1000,
      },
      drawer: {
        width: drawerWidth,
        flexShrink: 0,
        zIndex:999,
        background:grey[900],
        transitionDuration:"500ms"
      },
      drawerPaper: {
        width: drawerWidth,
      },
      toolbar: {
        marginTop:64
      },
      content: {
        flexGrow: 1,
        display:"flex",
        flexDirection:"column",
        height:"100%",
        backgroundColor: grey[900],
        
        paddingTop:0,
        paddingBottom:0,
      },
      headwrapper: {
        flexDirection:"row",
        justifyContent: "space-between"
      }
    }));
    const classes = useStyles();
    var Current = pages[page].page;
    var submitFn = async (e) => {
      e.preventDefault();
      setLoading(true)
      var frmData = new FormData();
      frmData.append("uid",user.userid)
      frmData.append("page",page+1)
      frmData.append("key",keyInput)
      var resp = await Axios.post(config.host+"api/validate.php",frmData);
      try {
        if(!resp.data.error) {
          setKeyInput("")
          setErrState(false);
          setErrStatus("")
          pages[page].status=true
          setPage(page)
        } else {
          setErrState(true);
          setErrStatus(resp.data.status)
        }
        
      } catch(e) {
        setErrState(true);
        setErrStatus("Please Check Your Internet Connection!")
      }
      setLoading(false);
    }
    if(!mainPage)
      return(
        <div style={{background:grey[900],height:"100%",width:"100%",display:"flex",justifyContent:"center",alignItems:"center"}}><CircularProgress /></div>
      )
    return(
        <>
            <ThemeProvider theme={theme2}>

            <Drawer className={classes.drawer} color="primary" variant="permanent" classes={{paper: classes.drawerPaper,}}
            open={state} onOpen={()=>setState(true)} onClose={()=>setState(false)} anchor="left">
                <div className={classes.toolbar} />
                <Divider />
                {
                  navState?
                  <List>
                    <ListItem style={{display:"flex",flexDirection:"row",justifyContent:"space-between",alignItems:"flex-start"}}>
                        <Avatar style={{background:avatarColor,fontSize:28,height:60,width:60,color:"#fff",textTransform:"uppercase"}} >{(user.name).substr(0,1)}</Avatar>
                        <IconButton size="small" onClick={()=>setNavState(!navState)}><ArrowBack></ArrowBack></IconButton>
                    </ListItem>
                    <ListItem style={{paddingBottom:0}}>
                        <Typography variant="subtitle1" style={{margin:0,fontWeight:500,fontSize:20,paddingBottom:0}}>{user.name}</Typography>
                    </ListItem>
                    <ListItem style={{paddingTop:0}}>
                        <Typography variant="subtitle2" style={{margin:0,color:grey[600],paddingTop:0}}>{user.email}</Typography>
                    </ListItem>
                  </List>:
                  <List style={{overflowY: "auto",display:"flex",flexDirection:"column",alignItems:"center"}}>
                    <ListItem style={{borderRadius:25,height:40,width:40,margin:"10px 0px 10px 0px",padding:0,display:"flex",justifyContent:"center",alignItems:"center"}}>
                        {/* <Avatar className="bg-blue" style={{background:lightBlue[500],fontSize:18,height:40,width:40,textTransform:"uppercase"}} >{(user.name).substr(0,1)}</Avatar> */}
                          <Avatar onClick={()=>setNavState(!navState)} style={{background:lightBlue[500],color:"#fff",fontSize:18,height:40,width:40,textTransform:"uppercase"}} >{(user.name).substr(0,1)}</Avatar>
                    </ListItem>
                  </List>
                }
                <Divider />
                <List style={{overflowY: "auto",display:"flex",flexDirection:"column",alignItems:"center"}}>
                {/* {["Instructions", "Page 1", "Page 2", "Page 3", "Page 4", "Page 5", "Page 6", "Page 7", "Page 8"].map((text, index) => ( */}
                {
                  navState?
                    ["Page 1", "Page 2", "Page 3", "Page 4", "Page 5", "Page 6", "Page 7", "Page 8", "Page 9"].map((text, index) => (
                        <Container key={text}>
                        <Paper style={{borderRadius:5}} elevation={5}>
                            <ListItem className={index==page?"headbg":""} style={{textAlign:"center",margin:"10px 0px 10px 0px",borderRadius:5}} onClick={() => {setPage(index)}} button key={text}>
                            <ListItemText primary={text} />
                            </ListItem>

                        </Paper>

                        </Container>
                    )):
                    ["1", "2", "3", "4", "5", "6", "7", "8", "9"].map((text, index) => (
                      <ListItem className={index==page?"headbg":"headbg2"}  style={{borderRadius:25,height:40,width:40,margin:"10px 0px 10px 0px",padding:0,display:"flex",justifyContent:"center",alignContent:"center",alignItems:"center"}} onClick={() => {setPage(index)}} button key={text}>
                          <ListItemText style={{textAlign:"center"}} primary={text} />
                      </ListItem>


                    ))
                  }
                </List>
            </Drawer>
            </ThemeProvider>
            <main className={classes.content} style={{display:"flex",flexDirection:"column", height: "100%", overflowY: "scroll"}}>
                <div className={classes.toolbar} />
                <Typography style={{marginTop:16,color:"#fff"}} align="center" variant="h4">Round 1</Typography>
                {
                  pages[page].status?  
                    <Current />:
                    <ThemeProvider theme={theme}>
                      <form onSubmit={submitFn} style={{height:"100%",width:"100%",maxWidth:300,display:"flex",flexDirection:"column",justifyContent:"center",alignItems:"center"}}>
                          <TextField label="KEY" style={{margin:"10px 0 10px 0"}} value={keyInput} error={errState} helperText={errStatus} required onChange={(e)=>{setKeyInput(e.target.value)}} color="secondary" variant="filled" type="text" fullWidth />
                          <Button variant="outlined" type="submit" style={{margin:"10px 0 10px 0"}} color="secondary">
                            Unlock
                            {
                              loading?<CircularProgress color="secondary" style={{margin:"0px 3px 0px 3px"}} size="1.5rem" />:null
                            }
                          </Button>
                      </form>
                    </ThemeProvider>

                }
            </main>        
        </>
    )
}
export default Round1;