import React from 'react';
import { Button, Paper, TextField } from "@material-ui/core";

import './page5.css'

class Page5 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            key: "17.688119, 83.277148",
            message: "E3pwMof8+TC1eXoox3M+ulKUpkiAEMegesTuPHtQwAA7OS8GYjoZYgvIUHPxGWnkJJbi1ryX7yr7Vc+5OIQMtA==",
            privateKey: ""
        };

        this.keyInput = null;

        this.check = this.check.bind(this);
        this.hint = this.hint.bind(this);
    }

    check() {
        console.log(this.keyInput);
        if (this.keyInput.value == this.state.key) {
            alert("Police: Thanks! you got the right information.");
        }
        else {
            alert("Police: Hey! Its wrong information. Be cool and think");
        }
    }

    hint() {
        var input = document.createElement("input");
        input.setAttribute("value", this.state.privateKey);
        input.setAttribute("id", "copy");
        document.body.appendChild(input);

        input = document.getElementById('copy');
        input.select();
        document.execCommand("copy");
        alert("You have aready got the private key");
        input.remove();
    }

    render() {
        return (
            <div className="round1">

            <div className="page5">
                <div className="header"></div>
                <div align="center">
                    <p>There is a drug dealer in your city. He has a deal with some one.</p>
                    <p>He was caught by the police men and found a paper with some letters on it.</p>
                    <p>They have taken the help of cryptography analyst and he found the "RSA" word in the paper.</p>
                    <p>Then he come know that the algorithm used to encrypt the <i>Secret Message</i> is <b>RSA</b>.</p>
                    <p>But to decrypt the cipher text (encrypted message), we need to have <b>private key</b>.</p>
                    <p>The police men come to know that the dealers used this website for private key transfer.</p>
                    <p>They tried hard to find the private key. So, they hired you.</p>
                    <p>Help them out to find the private key and decrypt the message given below with the online RSA decryption tools available.</p>
                    <p>Probably you may get the drug dealing location.</p>
                    <p>After decryption, enter the location below and send the location to the police men by using the below button.</p>
                    <p>You should find the key before the packages leaves the location.</p>
                </div>

                <div align="center">
                    <h3 style={{marginTop: "50px"}}>Cipher text</h3>
                    <Paper className="block" elevation={6}><p>{this.state.message}</p></Paper>
                </div>

                <div align="center">
                    You can use this online tool <a target="_blank" href="https://www.devglan.com/online-tools/rsa-encryption-decryption">Click here</a>
                </div>
                
                <div align="center" className="key">
                    <input label="Location"  className="keyInput" variant="standard" type="text" ref={(ref)=>{this.keyInput = ref}}/>
                    <Button 
                        color="primary" 
                        variant="contained"
                        onClick={this.check}
                    >Send Location</Button>
                    <Button style={{marginLeft: "10px"}} color="primary" variant="contained" onClick={this.hint} >Hint</Button>
                </div>
            </div>
            </div>
        );
    }
}

export default Page5;
