import React from "react";
import { AppBar, Divider, SwipeableDrawer as Drawer, Toolbar, Typography,ListItem,ListItemText,ListItemIcon,List, IconButton, Menu, MenuItem, Tabs, Tab, Avatar, Container, Paper, useMediaQuery } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { MoreVert, Translate,ArrowBack } from "@material-ui/icons";
import { makeStyles, ThemeProvider } from '@material-ui/core/styles';
import { lightBlue,grey,green,blueGrey,red,pink,purple, } from '@material-ui/core/colors';
import { createMuiTheme } from '@material-ui/core/styles';
import { BrowserRouter, Route, Switch, useHistory } from "react-router-dom";
import DashboardPage from "./DashboardPages/DashboardPage";
import Profile from "./DashboardPages/Profile";
import ChangePassword from "./DashboardPages/ChangePass";
import * as Colors from '@material-ui/core/colors';

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#AA00FF',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#fff',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
});


var drawerWidth = 280;
var randomProperty = function (obj) {
	var keys = Object.keys(obj);
	return obj[keys[ keys.length * Math.random() << 0]];
};
var avatarColor = randomProperty(Colors)[500];

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex:1000,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    zIndex:999
  },
  drawerPaper: {
    width: drawerWidth,
  },
  toolbar: {
	  height:64
  },
  content: {
	flexGrow: 1,
	background:grey[900],
  },
  headwrapper: {
    flexDirection:"row",
    justifyContent: "space-between"
  }
}));


// "Dashboard","Profile","Change Password"
var routes = [
    {name:"Change Password",path:"/ChangePassword",component:ChangePassword},
    // {name:"Profile",path:"/Profile",component:Profile},
    {name:"Dashboard",path:"/",component:DashboardPage}
]

function Dashboard({user}) {
	const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
    var [navState,setNavState] = React.useState(true);
    drawerWidth = navState?260:50;
    console.log(user);

    const theme = createMuiTheme({
      palette: {
        primary: {
          light: '#757ce8',
          main: '#AA00FF',
          dark: '#002884',
          contrastText: '#fff',
        },
        secondary: {
          light: '#ff7961',
          main: Colors.blue[600],
          dark: green[700],
          contrastText: '#000',
        },
        type: prefersDarkMode ? 'dark' : 'light',
      },
    });

    const classes = useStyles();
	let history = useHistory();
	var links = [...routes].reverse();
	var [state,setState]= React.useState(false);
	var temp =0;
	links.map((link,index) => {
		if(link.path==history.location.pathname)
			temp = index
	})
    var [page,setPage] = React.useState(temp);
    return(
        <>
            <ThemeProvider theme={theme}>

<Drawer className={classes.drawer} color="primary" variant="permanent" classes={{paper: classes.drawerPaper,}}
open={state} onOpen={()=>setState(true)} onClose={()=>setState(false)} anchor="left">
	<div className={classes.toolbar} />
	<Divider />
	  <List>
		<ListItem style={{display:"flex",flexDirection:"row",justifyContent:"space-between",alignItems:"flex-start"}}>
			<Avatar style={{background:avatarColor,fontSize:28,height:60,width:60,color:"#fff",textTransform:"uppercase"}} >{(user.name).substr(0,1)}</Avatar>
		</ListItem>
		<ListItem style={{paddingBottom:0}}>
			<Typography variant="subtitle1" style={{margin:0,fontWeight:500,fontSize:20,paddingBottom:0}}>{user.name}</Typography>
		</ListItem>
		<ListItem style={{paddingTop:0}}>
			<Typography variant="subtitle2" style={{margin:0,color:grey[600],paddingTop:0}}>{user.email}</Typography>
		</ListItem>
	  </List>
	<Divider />
                <List>
                {links.map((text, index) => (
                    <Container key={text.name}>
                        <Paper style={{borderRadius:5}} elevation={5}>
                            <ListItem className={index==page?"headbg":""} style={{textAlign:"center",margin:"10px 0px 10px 0px",borderRadius:5}} onClick={()=>{
								history.push(text.path)
								setPage(index)
                            }} button key={text.name}>
                                <ListItemText primary={text.name} />
                            </ListItem>
                        </Paper>
                    </Container>
                ))}
                </List>
</Drawer>
</ThemeProvider>
            <main className={classes.content}>
				<div className={classes.toolbar} />
				{/* <Typography align="center" variant="h4">Dashboard</Typography> */}
					<Switch>
						{
							routes.map((route) => 
								<Route key={route.name} name={route.name} path={route.path} render={()=><route.component user={user} />} />
							)
						}
					</Switch>
            </main>        
        </>
    )
}
export default Dashboard