import React from 'react';

import { Button, TextField, CircularProgress  } from '@material-ui/core';

import './page3.css';

class Page3 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            progressBar: false,
            isLoaded: false
        };

        this.show = this.show.bind(this);
    }

    async loadBLOB() {
        var blob = '';

        // this.setState({ blob });
        this.state.blob = blob;
        // this.state.isLoaded = true;
    }

    async show() {
        // this.setState({ progressBar: !this.state.progressBar });
        this.loadBLOB();
        this.setState({ isLoaded: !this.state.isLoaded });
    }

    render() {
        

        return (
            <div className="round2">
                <div className="page3">
                    <div className="desc">The BLOB of an image is given below. <br/>Decrypt it by using any online tool for further instructions.</div>
                    <Button variant="contained" color="primary" onClick={this.show}>Show</Button>
                    <div className="blob">
                        {/* {(this.state.progressBar)?<div>Loading...</div>:null} */}
                        {
                            (this.state.isLoaded)?<textarea>{this.state.blob}</textarea>:null
                        }
                    </div>
                </div>
            </div>
        );
    }
}

export default Page3;
