import React from 'react';

import { Button, TextField } from '@material-ui/core';
import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";

import './page4.css';

const theme = createMuiTheme({
    palette: {
      type: "dark"
    }
});

class Page4 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            adminPassword: "CTF@2.0",
            key: "YouGotTheAccessToken",
            displayKey: ""
        };

        this.usernameChange = this.usernameChange.bind(this);
        this.passwordChange = this.passwordChange.bind(this);
        this.login = this.login.bind(this);
        this.hideKey = this.hideKey.bind(this);
    }

    usernameChange(e) {
        this.setState({ username: e.target.value });
    }

    passwordChange(e) {
        this.setState({ password: e.target.value });
    }

    hideKey() {
        this.setState({ displayKey: "" });
    }

    login() {
        console.log(this.state.username, this.state.password);
        var username = this.state.username;
        var password = this.state.password;

        if ((username == "admin") && (password == this.state.adminPassword)) {
            alert("Lohin Success. \nNow the key is visible to you for less than a second for security reasons.");
            this.setState({ displayKey: "Key: "+this.state.key });
            setTimeout(this.hideKey, 200);
        }
        else {
            var input = document.createElement("input");
            input.setAttribute("value", this.state.adminPassword);
            input.setAttribute("id", "copy");
            document.body.appendChild(input);

            input = document.getElementById('copy');
            input.select();
            document.execCommand("copy");
            input.remove();

            alert("Invalid Credentials. \nSeems you already have password with you.");
        }
    }


    render() {
        return (
            <div className="round2">
                <div className="page4">
                    <div className="desc">Need your help to decrypt the admin password.<br/>It is known that the username used is always "admin"</div>
                    <div className="login">
                        <ThemeProvider theme={theme}>
                            <div className="title">Admin Login</div>
                            <TextField onChange={this.usernameChange} autoComplete="off" className="input" color="primary" id="username" label="Username" /><br />
                            <TextField onChange={this.passwordChange} type="password" className="input" id="password" label="Password" /><br />
                            <Button onClick={this.login} variant="contained" color="primary">Login</Button>
                        </ThemeProvider>
                    </div>
                    <div className="key">{this.state.displayKey}</div>
                </div>
            </div>
        );
    }
}

export default Page4;
