import React from "react";
import { AppBar, Divider, SwipeableDrawer as Drawer, Toolbar, Typography,ListItem,ListItemText,ListItemIcon,List, IconButton, Menu, MenuItem, Tabs, Tab, Avatar, Container, Paper, Button } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { MoreVert, Translate } from "@material-ui/icons";
import { makeStyles, ThemeProvider } from '@material-ui/core/styles';
import { lightBlue,grey } from '@material-ui/core/colors';
import './App.css';
import Home from './Layouts/Home';
import { createMuiTheme } from '@material-ui/core/styles';
import Dashboard from "./Layouts/Dashboard";
import Round1 from "./Layouts/Round1";
import Round2 from "./Layouts/Round2";
import { Route, BrowserRouter as Router, Switch,useHistory, useLocation  } from "react-router-dom";
import Login from "./Layouts/Login";
import Axios from "axios";
import config from './config.json';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import CssBaseline from '@material-ui/core/CssBaseline';



const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#AA00FF',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#fff',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
});


const drawerWidth = 280;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    height:"100%",
  },
  appBar: {
    zIndex:1000,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    zIndex:999
  },
  drawerPaper: {
    width: drawerWidth,
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  headwrapper: {
    flexDirection:"row",
    justifyContent: "space-between"
  }
}));


var routes = [
  {label:"Round 2",url:"/Round2",component:Round2},
  {label:"Round 1",url:"/Round1",component:Round1},
  {label:"Dashboard",url:"/",component:Dashboard},
]


function Main() {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: prefersDarkMode ? 'dark' : 'light',
        },
      }),
    [prefersDarkMode],
  );
  var user = localStorage.getItem("user");
  var [loginState,setLoginState] = React.useState(user!=null)
  var [userState,setUserState] = React.useState(user!=null?JSON.parse(user):null)
  var loginFn = async (data) => {
    var frm = new FormData();
    frm.append("email",data.email);
    frm.append("password",data.password);
    var resp = await Axios.post(config.host+"api/auth.php",frm);
    console.log(resp);
    if(!resp.data.error) {
      setUserState(resp.data.user)
      setLoginState(!resp.data.error)
      localStorage.setItem("user",JSON.stringify(resp.data.user))
    }
    return resp.data;
    // setLoginState(true)
  }
  var logoutFn = () => {
    localStorage.removeItem("user");
    setLoginState(false)
    setUserState(null)
  }
  return(
    <Router>
      {
        loginState?<App logout={logoutFn} user={userState} />:<Login login={loginFn} />
      } 
    </Router>
  )
}

function App({logout,user}) {
  let history = useHistory(),location = useLocation();
  const classes = useStyles();
  var [settings,setSetting] = React.useState(null);
  var links = [...routes]
  links.reverse();
  var temp = 0;
  links.map((link,index) => {
    if(link.url==location.pathname)
      temp = index;
  })
  
  const [tab, setTab] = React.useState(temp);
  // console.log(location);
  const tabChange = (event, newValue) => {
    setTab(newValue);
  };

  const handleClick = (event) => {
    setSetting(event.currentTarget);
  };
  return (
      <div className={classes.root}>
      <AppBar position="fixed" className={classes.appBar+" headbg"} elevation={0}>
        <Toolbar className={classes.headwrapper}>
          <Typography variant="h5">CTF 2.0</Typography>
          <ThemeProvider theme={theme}>
            <Tabs value={tab} onChange={tabChange} indicatorColor="secondary"  centered >
              {links.map((route, index) => (
                <Tab key={index} label={route.label} onClick={()=>{history.push(route.url)}} />
              ))}
            </Tabs>
          </ThemeProvider>
          <ThemeProvider theme={theme}>
            <div>

            <IconButton style={{color:"#fff"}} aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}><MoreVert /></IconButton>
            <Menu
              id="simple-menu"
              anchorEl={settings}
              keepMounted
              open={Boolean(settings)}
              onClose={()=>setSetting(null)}
              >
              <MenuItem onClick={()=>{setSetting(null)}}>Profile</MenuItem>
              <MenuItem onClick={()=>{setSetting(null)}}>Change Password</MenuItem>
              <MenuItem onClick={()=>{setSetting(null);logout()}}>Logout</MenuItem>
            </Menu>
            </div>
          </ThemeProvider>
        </Toolbar>
      </AppBar>
        <Switch>
          {routes.map(route => 
              <Route render={()=><route.component user={user} />} path={route.url} />
          )}
        </Switch>
    </div>
  );
}

export default Main;
